<template>
  <div class="tw-rounded bg-white mb-2">
    <div class="flex justify-center">
      <div class="w-full py-2 md:px-5">
        <div class="flex justify-between items-center px-3 md:p-0">
          <button class="w-8 h-8 mr-2" @click="$router.back()">
            <img src="/images/hero-back.svg" alt="back" />
          </button>
          <div class="mx-2 text-2xl">{{ wallName }}</div>
          <div class="flex space-x-2">
            <button :class="addTileClass" @click="onShowMyTile(true)">
              내 타일
            </button>
          </div>
        </div>
      </div>
    </div>

    <div v-if="errorMessage.length > 0" class="text-red-600 pb-2">
      {{ errorMessage }}
    </div>

    <div class="pb-0 md:pb-5 px-0 md:px-5">
      <div class="flex justify-center item-start">
        <iframe
          class="tw-iframe"
          ref="visitWallFrame"
          src="/kbEditWall/main.html"
          :width="frameWidth"
          :height="frameHeight"
          scrolling="no"
          frameborder="0"
        >
        </iframe>
      </div>
    </div>

    <!-- MY TILE LIST MODAL -->
    <div
      class="
        absolute
        inset-0
        bg-black bg-opacity-50
        flex
        justify-center
        items-start
        h-full
        p-5
      "
      v-if="showMyTileList"
    >
      <div class="relative w-full rounded-lg bg-white p-2">
        <div class="flex justify-between items-center">
          <div>내 타일을 붙일 수 있습니다.</div>
          <button class="p-1 w-8 h-8" @click="showMyTileList = false">
            <img src="/images/hero-x-outline.svg" />
          </button>
        </div>

        <div class="p-2 h-96 rounded-lg overflow-y-auto bg-gray-300">
          <div class="grid grid-cols-3 gap-2">
            <button
              class="shadow rounded-lg overflow-hidden bg-white"
              v-for="data in listPic"
              :key="data.rid"
              @click="onSelectTile(data)"
            >
              <img class="w-full" :src="$store.makePicUrl(data.pic_url)" />
              <div>{{ data.pic_name }}</div>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      iframeWin: null,

      wallRid: 0,
      wallName: "",
      wallUserName: "",

      loading: false,
      loadingSuccess: false,
      errorMessage: "",
      frameWidth: "100%",
      frameHeight: "100%",

      showMyTileList: false,
      enableAddTile: false,
    };
  },
  async mounted() {
    this.iframeWin = this.$refs.visitWallFrame.contentWindow;
    console.log("mounted this.iframeWin", this.iframeWin);
    this.wallRid = parseInt(this.$route.params.rid, 10);
    console.log("WallVisit", this.wallRid);

    window.addEventListener("message", this.procMessage);
  },
  unmounted() {
    window.removeEventListener("message", this.procMessage);
  },
  computed: {
    addTileClass() {
      return this.enableAddTile ? "tw-indigo-btn" : "tw-gray-btn";
    },
    listPic() {
      if (Array.isArray(this.$store.state.listPicture)) {
        const arr = [];
        this.$store.state.listPicture.forEach((data) => {
          if (data.pic_url == "") return;
          arr.push(data);
        });
        return arr;
      }
      return [];
    },
  },
  methods: {
    async procMessage(event) {
      if (event.data.cmd === undefined) return;
      if (event.data.cmd.indexOf("kbEditWall") < 0) return;

      const recv = event.data;
      console.log("WallVisit procMessage", recv);

      if (recv.cmd == "kbEditWall.loadingComplete") {
        await this.$store.wait(1000);

        const reqData = {
          api: "getWall",
          loginToken: localStorage.getItem("loginToken"),
          userUid: this.$store.getters.userUid,
          wallRid: this.wallRid,
          needPic: this.$store.state.listPicture === null,
          isVisit: true,
        };

        const res = await this.$store.callAsync(reqData);
        if (res.code != 100) {
          this.errorMessage = res.message;
          return;
        }

        console.log("getWall res", res);
        this.wallName = res.wall.wall_name;
        // if (res.listPicture !== undefined) {
        //   this.$store.commit("setListPicture", res.listPicture);
        // }

        this.$store.postMessage(this.iframeWin, {
          cmd: "kbEditWall.firstData",
          user_uid: this.$store.getters.userUid,
          mode: "visit",
          wall_data: res.wall.wall_data,
          visit_list: res.visit_list,
        });
      } else if (recv.cmd == "kbEditWall.frameSize") {
        console.log("frameSize", recv.w, recv.h);
        this.frameWidth = recv.w;
        this.frameHeight = recv.h;
      } else if (recv.cmd == "kbEditWall.setEnableAddTile") {
        this.enableAddTile = recv.enable;
      } else if (recv.cmd == "kbEditWall.saveAtVisit") {
        const tile = recv.tile;

        const reqData = {
          api: "saveWallVisit",
          loginToken: localStorage.getItem("loginToken"),
          userUid: this.$store.getters.userUid,
          wallRid: this.wallRid,
          tile: tile,
        };

        const res = await this.$store.callAsync(reqData);
        await this.$store.wait(1000);

        if (res.code != 100) {
          console.log("saveWallVisit err", res);
          this.errorMessage = res.message;
          this.$store.postMessage(this.iframeWin, {
            cmd: "kbEditWall.saveComplete",
            success: false,
          });
          return;
        }

        console.log("saveWallVisit res", res);
        this.$store.postMessage(this.iframeWin, {
          cmd: "kbEditWall.saveComplete",
          success: true,
        });
      } else {
        console.error("WallVisit unknown cmd", recv.cmd);
      }
    },
    onShowMyTile(show) {
      if (show) {
        if (!this.enableAddTile) {
          return;
        }
      }
      this.showMyTileList = show;
    },
    onSelectTile(tile) {
      console.log("onSelectTile", tile);
      // console.log("onSelectTile this.iframeWin", tile);
      this.showMyTileList = false;

      this.$store.postMessage(this.iframeWin, {
        cmd: "kbEditWall.addImageAtVisit",
        tile: {
          id: `${tile.rid}`, // 문자열로 가야함
          url: tile.pic_url,
        },
      });
    },
  },
};
</script>
